body {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.14;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
h1::selection,
h2::selection,
h3::selection,
h4::selection,
p::selection,
span::selection,
a::selection,
li::selection {
  background-color: getcolor(accent-color);
  color: getcolor(white-text-color);
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: currentColor;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

ul,
li {
  list-style-type: none;
  color: currentColor;
  padding: 0;
  margin: 0;
  cursor: inherit;
}
img {
  display: inline-block;
  width: 100%;
  max-height: 100%;
  height: auto;
  cursor: inherit;
}
