.cards {
  padding-bottom: 40px;

  @media screen and (min-width: 768px) {
    padding-bottom: 60px;
  }
}

.cards__film {
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    justify-content: center;
    margin: 0;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
  }
}

.movie {
  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 32px) / 2);
  }

  @media screen and (min-width: 1280px) {
    flex-basis: calc((100% - 32px) / 3);
  }
}

img {
  border-radius: 5px;
  margin-bottom: 10px;
  display: block;
  width: 280px;
  height: 402px;
  background-color: black;
  @media screen and (max-width: $phone) {
    width: 100%;
    height: auto;
  }

  @media screen and (min-width: 768px) {
    width: 336px;
    height: 455px;
    margin-bottom: 8px;
  }

  @media screen and (min-width: 1280px) {
    width: 394px;
    height: 574px;
    margin-bottom: 12px;
  }
}

.movie__title {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(14 / 12);
  text-transform: uppercase;
  color: #000000;

  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}

.movie__position {
  justify-content: flex-start;
  align-items: center;
  display: flex;
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
}

.movie__genres {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: #b92f2c;
  margin-right: 5px;

  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}

.movie__year {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: #b92f2c;

  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}
